import axios from 'axios'
import { Message } from 'element-ui'
import store from '../store'
import { getToken } from '@/utils/auth'

// 创建axios实例
const service = axios.create({
  baseURL: process.env.BASE_API, // api的base_url
  timeout: 15000,                  // 请求超时时间
  // headers: { 'Content-Type': 'application/x-www-form-urlencoded' },
  transformRequest: [(data) => {
    if (data && data.headersMethod && data.headersMethod === 'json') {
      return JSON.stringify(data.params)
    } else {
      let ret = '';
      for (const it in data) {
        if (Object.prototype.hasOwnProperty.call(data, it)) {
          ret = `${ret + encodeURIComponent(it)}=${encodeURIComponent(data[it])}&`;
        }
      }
      return ret;
    }
  }]
})

// request拦截器
service.interceptors.request.use(config => {
  let jsonUrlArr = []
  jsonUrlArr = [
    'room/publish',
    'room/publishHot'
  ]
  if (store.getters.token) {
    config.headers['X-Token'] = getToken() // 让每个请求携带自定义token 请根据实际情况自行修改
  }
  if (jsonUrlArr.indexOf(config.url) !== -1) {
    config.headers['Content-Type'] = 'application/json'
  } else {
    config.headers['Content-Type'] = 'application/x-www-form-urlencoded'
  }
  // 线上 config.url = 'http://www.aixifan.com/chatroom/cms/' + config.url
  config.url = location.origin + '/chatroom/cms/' + config.url
  return config
}, error => {
  // Do something with request error
  console.log(error) // for debug
  Promise.reject(error)
})

// respone拦截器
service.interceptors.response.use(
  response => {
  /**
  * code为非200是抛错
  */
    const res = response.data
    if (res.code !== 200) {
      Message({
        message: res.message,
        type: 'error',
        duration: 5 * 1000
      })

      // 50008:非法的token; 50012:其他客户端登录了;  50014:Token 过期了;
      /* if (res.code === 50008 || res.code === 50012 || res.code === 50014) {
        MessageBox.confirm('你已被登出，可以取消继续留在该页面，或者重新登录', '确定登出', {
          confirmButtonText: '重新登录',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          store.dispatch('FedLogOut').then(() => {
            location.reload()// 为了重新实例化vue-router对象 避免bug
          })
        })
      }*/
      return Promise.reject('error')
    } else {
      return response.data
    }
  },
  error => {
    console.log('err' + error)// for debug
    Message({
      message: error.message,
      type: 'error',
      duration: 5 * 1000
    })
    return Promise.reject(error)
  }
)

export default service



